import { versionStringToNumber } from '@popup/shared/helpers/versionStringToNumber';
import {
  type FeatureListResponse,
  type FeatureList,
  Feature,
} from '../types/Feature';

function getActive(featureVersion?: string | null): boolean {
  const currentVersion = chrome.runtime.getManifest().version;

  if (!featureVersion) {
    return false;
  }

  return (
    versionStringToNumber(currentVersion) >=
    versionStringToNumber(featureVersion)
  );
}

export function formatResponse(
  featureListDto: FeatureListResponse,
): FeatureList {
  const featureList: FeatureList = {
    [Feature.SwitchYoutubeInternet]: getActive(
      featureListDto[Feature.SwitchYoutubeInternet],
    ),
    [Feature.DevMeta]: getActive(featureListDto[Feature.DevMeta]),
    [Feature.PACLite]: getActive(featureListDto[Feature.PACLite]),
    [Feature.CaptchaCrackerDisabled]: getActive(
      featureListDto[Feature.CaptchaCrackerDisabled],
    ),
    [Feature.OnFailDisabled]: getActive(featureListDto[Feature.OnFailDisabled]),
    [Feature.ProxyControlledByOtherExtensionsVisibility]: getActive(
      featureListDto[Feature.ProxyControlledByOtherExtensionsVisibility],
    ),
    [Feature.AdfoxBanner]: getActive(featureListDto[Feature.AdfoxBanner]),
    [Feature.MultiDeviceSupport]: getActive(
      featureListDto[Feature.MultiDeviceSupport],
    ),
    [Feature.ProxyAuth]: getActive(featureListDto[Feature.ProxyAuth]),
    [Feature.FreeUsersReview]: getActive(
      featureListDto[Feature.FreeUsersReview],
    ),
    [Feature.MultiLocalesSupport]: getActive(
      featureListDto[Feature.MultiLocalesSupport],
    ),
  };

  return featureList;
}
