import { PremiumAlarms } from '../../alarms/premium';
import { createSentryScope } from '@app/sentry/createSentryScope';
import { ExtensionMonitoringData } from '@background/extensions/extensionMonitor';
import { CookieKey, getCookieData, setCookieData } from '../cookies/shared';

export const localStorageScope = createSentryScope('storage');

export enum LocalStorageKey {
  FeedbackProvided = 'feedbackProvided',
  TargetState = 'targetState',
  CurrentState = 'currentState',
  Theme = 'theme',
  ConnectionCount = 'connectionCount',
  DeviceId = 'deviceId',
  /**
   * @deprecated - will remove after backend fix
   */
  DeviceIp = 'deviceIp',
  IsBrowserError = 'isBrowserError',
  PACData = 'pacData',
  PACRegistryData = 'pacRegistryData',
  AlarmTries = 'alarmTries',
  OnboardingScreenStatus = 'onboardingScreenStatus',
  BrowserProxySettings = 'browserProxySettings',
  SavedTime = 'savedTime',
  IsLitePAC = 'isLitePAC',
  ExtensionMonitoring = 'extensionMonitoring',
  UserId = 'userId',
  CookieLoaded = 'cookieLoaded',
  FeatureStore = 'featureStore',
  FeaturesListLocal = 'featuresListLocal',
  ConfigsBaseUrl = 'configsBaseUrl',
  ErrorMessage = 'errorMessage',
  ProxyData = 'proxyData',
  IsInitProxyAuthStarted = 'isInitProxyAuthStarted',
}

export type ConnectionState =
  | 'connected'
  | 'disconnected'
  | 'connecting'
  | 'error';

type Theme = 'light' | 'dark';

export type PACData = {
  free: string;
};

export type PACRegistryData = {
  free: string;
  premium: string;
  freeLite?: string;
  premiumLite?: string;
  updatedAt: string;
};

export type BrowserProxySettings = {
  levelOfControl: chrome.types.ChromeSettingGetResultDetails['levelOfControl'];
  pacScript?: string;
};

export type AlarmTries = Partial<Record<PremiumAlarms, number>>;

export type OnboardingScreenStatus =
  | 'step-zero'
  | 'step-one'
  | 'step-two'
  | 'step-three'
  | 'step-four'
  | 'step-five'
  | 'step-six'
  | 'done';

export type LocalStorageData = {
  [LocalStorageKey.FeedbackProvided]?: boolean;
  [LocalStorageKey.TargetState]?: 'connected' | 'disconnected';
  [LocalStorageKey.CurrentState]?: ConnectionState;
  [LocalStorageKey.Theme]?: Theme;
  [LocalStorageKey.ConnectionCount]?: number;
  [LocalStorageKey.DeviceId]?: string;
  [LocalStorageKey.DeviceIp]?: string;
  [LocalStorageKey.IsBrowserError]?: boolean;
  [LocalStorageKey.PACData]?: PACData;
  [LocalStorageKey.PACRegistryData]?: PACRegistryData;
  [LocalStorageKey.AlarmTries]?: AlarmTries;
  [LocalStorageKey.OnboardingScreenStatus]?: OnboardingScreenStatus;
  [LocalStorageKey.BrowserProxySettings]?: BrowserProxySettings;
  [LocalStorageKey.SavedTime]?: Record<string, number>;
  [LocalStorageKey.IsLitePAC]?: boolean;
  [LocalStorageKey.ExtensionMonitoring]?: ExtensionMonitoringData;
  [LocalStorageKey.UserId]?: string;
  [LocalStorageKey.CookieLoaded]?: boolean;
  [LocalStorageKey.FeatureStore]?: Record<string, unknown>;
  [LocalStorageKey.FeaturesListLocal]?: Record<string, boolean>;
  [LocalStorageKey.ConfigsBaseUrl]?: string;
  [LocalStorageKey.ErrorMessage]?: string;
  [LocalStorageKey.ProxyData]?: Record<string, unknown>;
  [LocalStorageKey.IsInitProxyAuthStarted]?: boolean;
};

export async function getLocalStorageData<Key extends LocalStorageKey>(
  keys: Key[] | Key,
): Promise<
  | {
      [P in Key[][number]]: LocalStorageData[P];
    }
  | LocalStorageData
> {
  if (!Array.isArray(keys)) {
    return chrome.storage.local.get(keys) as Promise<{
      [P in Key[][number]]: LocalStorageData[P];
    }>;
  }
  return chrome.storage.local.get(keys) as Promise<LocalStorageData>;
}

export async function setLocalStorageData(
  data: Partial<LocalStorageData>,
): Promise<void> {
  await chrome.storage.local.set(data);
}

export async function deleteLocalStorageData(
  keys: LocalStorageKey[],
): Promise<void> {
  await chrome.storage.local.remove(keys);
}

// We need to update the userId in cookies when it changes in local storage
chrome.storage.local.onChanged.addListener(async (changes) => {
  if (changes[LocalStorageKey.UserId]) {
    const { userId: cookieUserId } = await getCookieData(CookieKey.UserId);
    const newUserId = changes[LocalStorageKey.UserId].newValue;

    if (newUserId && newUserId !== cookieUserId) {
      setCookieData({
        [CookieKey.UserId]: newUserId,
      });
    }
  }

  if (changes[LocalStorageKey.DeviceId]) {
    const { deviceId: cookieDeviceId } = await getCookieData(
      CookieKey.DeviceId,
    );
    const newDeviceId = changes[LocalStorageKey.DeviceId].newValue;

    if (newDeviceId && newDeviceId !== cookieDeviceId) {
      setCookieData({
        [CookieKey.DeviceId]: newDeviceId,
      });
    }
  }
});
